import './App.css';
import {BrowserRouter , Link, Route, Redirect } from 'react-router-dom'
import Home from './Home'
import Admin from './Admin'
import Login from './Login'

import { useState } from 'react';


function App(props) { //console.log(props.husnegt[0].nas1[1])
  //state props
  let [user, setUser] = useState({}); 
console.log('App=>', user)

  return (
    <BrowserRouter>
      
      {/* <Link to='angilal'> Ангилал</Link> 
      <Link to='admin'> admin</Link> 
      <Link to='login'> login</Link>  */}

      <Route exact path='/'>  <Home/></Route>
      <Route exact path='/githubpages/ulstor'>  <Home category='ulstor'/></Route>
      <Route exact path='/githubpages/edZasag'>  <Home category='edZasag'/></Route>
      <Route exact path='/githubpages/gadaad'>  <Home category='gadaad'/></Route>

      {/* <Route path='/admin' hereglegch={user}>  <Admin/></Route> */}
      <Route path='/githubpages/login'>  <Login hereglegch={setUser}/></Route>

      <Route exact path="/githubpages/admin">
        {user && user.token ? <Admin />  : <Redirect to="/login" />}
      </Route>
      <Route path='/githubpages/admin/medee'><Admin category='medee'/></Route>
      <Route path='/githubpages/admin/angilal'><Admin category='angilal'/></Route>
      <Route path='/githubpages/admin/user'><Admin category='user'/></Route>
      


    </BrowserRouter>
  );
}

export default App;
