import Medee from './Medee'
import Angilal from './Angilal'
import User from './User'

import { Link } from "react-router-dom";


function Admin(props){
    function renderComp(){
        if(props.category ==='medee') return <Medee/>
        if(props.category ==='angilal') return <Angilal/>
        if(props.category ==='user') return <User/>
    }
    return (
        <div>
            <Link to='/githubpages/admin/medee'> Мэдээ</Link>
            <Link to='/githubpages/admin/angilal'> Ангилал</Link>
            <Link to='/githubpages/admin/user'> Хэрэглэгч</Link>
            {renderComp()}

        </div>
    )
}

export default Admin;