import {useHistory, useLocation, useParams, useRouteMatch} from 'react-router-dom';
import {useState} from 'react'

function Login(props){ console.log('logn=>>', props)
    const [em, setEm] = useState('')
    const [pass, setPass] = useState('')


    let history = useHistory(); //hooks
    let location = useLocation(); 
    let params = useParams(); 
    let match = useRouteMatch(); 
console.log('login======> hisotory', history )
console.log('login======> location', location )
console.log('login======> params', params )
console.log('login======> match', match )

function burtguuleh(){
    //end jinhene backend ruu holbogdoj user bolon token avah code bn

    let req = new XMLHttpRequest();
    
    req.open('POST', 'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyD45M3YyfFFAjfjQrgihirerxrlAaDx3jg');
    req.setRequestHeader('Content-Type', 'application/json');
    req.responseType = 'json';
    req.onload = function () {
        console.log('res', req.response);
        props.hereglegch({
            email: req.response.email,
            token: req.response.idToken,
            id: req.response.localId
        })


        history.push('admin')
    };
    req.send(JSON.stringify({email: em, password: pass, returnSecureToken: true}));

    

}


    function nevtreh(){
        //end jinhene backend ruu holbogdoj user bolon token avah code bn

        let req = new XMLHttpRequest();
        
        req.open('POST', 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyD45M3YyfFFAjfjQrgihirerxrlAaDx3jg');
        req.setRequestHeader('Content-Type', 'application/json');
        req.responseType = 'json';
        req.onload = function () {
            console.log('res', req.response);
            props.hereglegch({
                email: req.response.email,
                token: req.response.idToken,
                id: req.response.localId
            })


            history.push('admin')
        };
        req.send(JSON.stringify({email: em, password: pass, returnSecureToken: true}));

        

    }


    return (
        <div>
            Нэвтрэхх цонх<br></br>
            <input className='adfds' type='text' onChange={   (e)=>{setEm(e.target.value)}  }></input><br></br>
            <input type='password' onChange={   (e)=>{setPass(e.target.value)}  }></input><br></br>
            <input type='button' value='Нэвтрэх' onClick={nevtreh}></input>
            <input type='button' value='Нэвтрэх' onClick={burtguuleh}></input>

        </div>
    )
}

export default Login;