import Ulstor from "./Ulstor";
import Ez from "./Ez";

const { Link } = require("react-router-dom");
function Home(props){
    function renderComp(){
        if(props.category ==='ulstor') return <Ulstor/>
        if(props.category ==='edZasag') return <Ez/>
        // if(props.category ==='user') return <User/>
    }
    return (
        <div >
            <Link to='/githubpages/ulstor' > Улстөр</Link>
            <Link to='/githubpages/edZasag'> ЭЗ</Link>
            <Link to='/githubpages/gadaad'> Гадаад</Link>

            {renderComp()}
        </div>
    )
}

export default Home;